"use client";
import Image from "next/image";
import { useEffect, useState } from "react";
import { ArrowUp } from "lucide-react";
import { useTheme } from "next-themes";

export default function Navbar() {
  const [showButton, setShowButton] = useState(false);
  const { theme } = useTheme();
  const [mounted, setMounted] = useState(false);

  // ✅ Show scroll-to-top button
  useEffect(() => {
    const handleScroll = () => setShowButton(window.scrollY > 300);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // ✅ Scroll smoothly to top
  const scrollToTop = () => window.scrollTo({ top: 0, behavior: "smooth" });

  // ✅ Avoid hydration mismatch
  useEffect(() => setMounted(true), []);
  if (!mounted) return null;

  return (
    <>
      {/* ✅ Fixed Navbar */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-[var(--color-bg-neutral-50)] border-b border-[var(--color-btn-teritary-200)] shadow-sm">
        <div className="mx-auto max-w-7xl px-8 py-3 flex items-center justify-start">
          {/* ✅ Logo Section shifted slightly to the right */}
          <div
            onClick={scrollToTop}
            className="flex items-center gap-2 cursor-pointer transition-transform duration-300 hover:scale-[1.02] ml-[40px]"
          >
            <Image
              src="/logo.png"
              alt="Workly"
              width={110}
              height={32}
              priority
              className="object-contain"
            />
            <span className="px-3 py-0.5 text-xs font-medium rounded-full bg-[var(--color-btn-teritary-100)] border border-[var(--color-btn-teritary-200)] text-[var(--color-text-primary)]">
              Pro
            </span>
          </div>
        </div>
      </header>

      {/* ✅ Spacer to avoid content overlap */}
      <div className="h-[45px]" />

      {/* ✅ Scroll-to-Top Button */}
      {showButton && (
        <button
          onClick={scrollToTop}
          aria-label="Scroll to top"
          className={`fixed bottom-6 right-6 z-50 flex items-center justify-center rounded-full 
          backdrop-blur-md bg-[var(--color-bg-neutral-50)]/90 border border-[var(--color-btn-teritary-200)] 
          shadow-md text-[var(--color-text-primary)] transition-all duration-300 
          hover:bg-[var(--color-btn-teritary-100)] hover:shadow-lg cursor-pointer
          ${
            showButton
              ? "opacity-100 translate-y-0"
              : "opacity-0 translate-y-6 pointer-events-none"
          }`}
          style={{ width: 50, height: 50 }}
        >
          <ArrowUp size={22} strokeWidth={2.5} />
        </button>
      )}
    </>
  );
}
